<template>
  <div class="home">
      <h2 class='RoomNum'>房间数量:{{ roomNum }}</h2>
      <h3 class='RoomTitle' v-for='room in rooms' :key='room.name'>{{room.name}}</h3>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from "axios";

export default {
  name: "home",
  data() {
    return {
      rooms: []
    };
  },
  computed: {
    roomNum() {
      return this.rooms.length;
    }
  },
  components: {},
  methods:{
    async fetchRooms(){
      try {
        const response = await axios.get(this.$serverHost() + "control/allRoom");
        // console.log(response);
        if (response.data.errno != 0) throw new Error(response.data.errmsg);
        this.rooms = response.data.data;
      } catch (error) {
        console.error(error);
      }
    }
  },
  socket: {
    // Prefix for event names
    // prefix: "/counter/",

    // If you set `namespace`, it will create a new socket connection to the namespace instead of `/`
    // namespace: "/counter",
    events: {
      // Similar as this.$socket.on("changed", (msg) => { ... });
      // If you set `prefix` to `/counter/`, the event name will be `/counter/changed`
      //
      opend(msg) {
        console.log(msg);
      },
      roomChange(){
        this.fetchRooms()
      }
    }
  },
  mounted() {
    this.fetchRooms()
  }
  
};
</script>
<style scoped>
.home {
  padding: 0 50px;
}
.RoomNum {
  text-align: left;
}
.RoomTitle {
  text-align: left;
  margin-left: 2em;
}
</style>
